  public cancelOrderF(
    orderId: ImmutableMethodParams.ImmutableCancelOrderParamsTS,
  ): TE.TaskEither<Error, ImmutableMethodResults.ImmutableCancelOrderResult> {
    return pipe(
      TE.of({ orderId }),
      TE.bind('signature', ({ orderId }) =>
        this.wallet.controller.cancelOrder(orderId.toString()),
      ),
      TE.bind('data', ({ signature }) => {
        return TE.of({
          stark_signature: signature,
        });
      }),
      TE.bind('signableResult', ({ orderId }) =>
        this.post(
          '/signable-cancel-order-details',
          JSON.stringify({ order_id: orderId }),
          ImmutableMethodResults.ImmutableGetSignableCancelOrderResultCodec
            .decode,
        ),
      ),
      TE.bind('ethSignature', ({ signableResult }) =>
        this.signEthMessage(signableResult.signable_message),
      ),
      TE.bind('result', ({ data, ethSignature }) =>
        this.delete(
          `/orders/${orderId}`,
          data,
          ImmutableMethodResults.ImmutableCancelOrderResultCodec.decode,
          addEthHeaders({}, ethSignature.ethAddress, ethSignature.ethSignature),
        ),
      ),
      TE.map(({ result }) => result),
    );
  }

