  /**
   * Create a moonpay signature based on the provided request string
   */
  public async getMoonpaySignature(
    req: ImmutableMethodResults.ImmutableGetMoonpaySignatureRequest,
  ): Promise<ImmutableMethodResults.ImmutableGetMoonpaySignatureResult> {
    const validatedRequest = valueOrThrow(
      ImmutableGetMoonpaySignatureRequestCodec.decode(req),
    );
    const reqParam = {
      ...validatedRequest,
      walletAddresses:
        'walletAddresses' in validatedRequest
          ? JSON.stringify(validatedRequest.walletAddresses)
          : undefined,
    };

    return this.handleSignatureRequest(reqParam);
  }

  /**
   * Create a moonpay sell signature based on the provided request string
   */
  public async getMoonpaySellSignature(
    req: ImmutableMethodResults.ImmutableGetMoonpaySellSignatureRequest,
  ): Promise<ImmutableMethodResults.ImmutableGetMoonpaySignatureResult> {
    const validatedRequest = valueOrThrow(
      ImmutableGetMoonpaySellSignatureRequestCodec.decode(req),
    );
    const reqParam = {
      ...validatedRequest,
      refundWalletAddresses:
        'refundWalletAddresses' in validatedRequest
          ? JSON.stringify(validatedRequest.refundWalletAddresses)
          : undefined,
    };

    return this.handleSignatureRequest(reqParam);
  }

  async handleSignatureRequest(body) {
    const reqParam = {
      ...body,
      showOnlyCurrencies: body.showOnlyCurrencies
        ? body.showOnlyCurrencies.join(',')
        : undefined,
    };

    const result = await this.requests.post(
      'moonpay/sign-url',
      {
        request: queryString.stringify(reqParam),
      },
      undefined,
      V2,
    );
    return valueOrThrow(ImmutableGetMoonpaySignatureResultCodec.decode(result));
  }

